@use "cams-colors";
@use "colors";

$text-size-xxs: 8px;
$text-size-xs: 10px;
$text-size-small: 12px;
$text-size-low: 13px;
$text-size-default: 14px;
$text-size-big: 15px;
$text-size-heading-sm: 16px;
$text-size-heading: 18px;
$text-size-heading-lg: 20px;
$text-size-heading-lg-2: 22px;
$text-size-heading-lg-3: 23px;
$text-size-heading-large: 24px;
$text-size-heading-xl: 28px;
$text-size-heading-xxl: 32px;
$text-size-heading-xxxl: 36px;

// shadows
$box-shadow:
  0 4px 10px 0 cams-colors.$ninja,
  0 2px 7px 0 cams-colors.$hadopelagic-water,
  0 0 2px 0 cams-colors.$abyssopelagic-water;

// borders
$table-border: 1px solid colors.$grey-20;
