@use "cams-variables";
@use "colors";

:host {
  ::ng-deep {
    .mill-button_appearance_hidden {
      &:not(:disabled) {
        &:hover {
          background-color: colors.$grey-20;
        }
      }
    }
  }
}

.application-details-history-comment {
  border: 1px solid colors.$grey-30;
  border-radius: 8px;
  padding: 17px;
  transition: background-color 0.2s ease-in-out;

  &:hover {
    background-color: colors.$grey-10;
  }

  &__inner {
    position: relative;
    display: flex;
    flex-direction: column;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-block-end: 5px;

    &-name {
      font-size: cams-variables.$text-size-heading-sm;
      margin: 0;
    }
  }

  &__text {
    color: colors.$grey-50;
    line-height: 1.6;
    font-size: cams-variables.$text-size-default;
  }

  &__link {
    position: absolute;
    right: 15px;
    top: 33%;
    height: 25px;
    color: colors.$grey-50;
    cursor: pointer;
  }
}
